.modal-header {
  font-family: "Nunito", sans-serif;
  font-weight: 800;
  font-size: 14px;
  color: #000000;
  background: #ffffff;
}

div.modal-content {
  color: #000000;
  background: #ffffff;

}

.list-wrapper-modal {
  padding: 16px;
  margin: 0px auto 0;
  max-width: 768px;
  border-radius: 4px;
  box-shadow: 8px 8px 8px 8px rgba(0, 0, 0, .1);
}

.list-form-modal {
  margin-bottom: 12px;
}

.form-input-modal {
  padding: 4px;
  font-size: 12px;
  border: 1px solid #dedede;
  border-radius: 4px;
}

.form-label-modal {
  display: inline-flex;
  margin-right: 6px;
  margin-left: 6px;
  font-size: 12px;
  color: #000000;
}

.form-wrapper-modal fieldset {
  padding: 0;
  margin: 0;
  border: 0;
  font-size: 12px;
}

.fieldset-modal {
  margin-top: 10px;
}
